
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import intelligentServiceList from '../../components/IntelligentServiceList';
import tools from '../../lib/tools.js';
export default {
name:'topicIndex',
components:{intelligentServiceList,Flexbox, FlexboxItem },
data(){
return {
errorImg:'this.src="' + require('../../assets/morenshangpin3x.png') + '"' ,
serviceObj:{}
}
},
mounted(){
let vm = this;
let id = tools.getRequestParam('serviceId');
if(!id){return false;}
this.$http.axios({method:'POST',url:'/implement/queryDetailFind',data:{id}}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200) && data.resultCode){
vm.serviceObj = data.data;
let tempService = [];
for(var i=0;i<data.data.implementList.length;i++){
let item = data.data.implementList[i];
if(item.serviceList){
tempService.push({type:1,url:item.name,service:item.serviceList[0],imgUrl:item.imgUrl});
}else if(item.goodsList){
tempService.push({type:0,url:item.name,service:item.goodsList[0],imgUrl:item.imgUrl});
}
}

tempService && (vm.serviceObj.implementList = tempService);
document.getElementById('content').innerHTML = data.data.description;
const imgs = document.querySelectorAll('#content img')
for(var i=0;i<imgs.length;i++){
imgs[i].className = 'img-responsive';
imgs[i].style.display = 'block';
imgs[i].style.maxWidth = '100%';
imgs[i].style.height = 'auto';
}
}else{
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
console.error(data);
}
},(error) => {
console.error(error);
})
},
methods:{
goTo(service){
console.log(service);
if(!service.url){return false;}

sessionStorage.setItem('intrlligentUrl',service.url);
if(service.url.indexOf('/goods')==0){
window.location.href = '/m/equipmentIndex.html?isBuy=1&goodId='+service.service.id+'&sourceType=0&_='+(new Date().getTime());
}else{
window.location.href = '/m/serviceIndex.html?serviceId='+service.service.id+'&_='+(new Date().getTime());
}
}
}
}



