
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

const FastClick = require('fastclick')
FastClick.attach(document.body);
export default {
name:'intelligent',
components:{Flexbox, FlexboxItem,Toast,Divider},
props:['type'],
data(){
return {
errorImg:'this.src="' + require('../assets/morenshangpin3x.png') + '"' ,
showToast:false,
toastMsg: '',
services:[],
n: 10,
status: {
pullupStatus: 'default',
pulldownStatus: 'default'
},
pullupEnabled:true
}
},
mounted(){
let vm = this;


let tempType = this.type || 1;
this.$http.post('/implement/query?type='+tempType,{}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
let tempService = [];
for(var i=0;i<data.data.length;i++){
let item = data.data[i];
if(item.serviceList){
tempService.push({type:1,url:item.name,service:item.serviceList[0],imgUrl:item.imgUrl});
}else if(item.goodsList){
tempService.push({type:0,url:item.name,service:item.goodsList[0],imgUrl:item.imgUrl});
}
}
tempService && (vm.services = tempService);
}else{
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
console.error(data);
}
},(error) => {
console.error(error);
})
},
methods:{
goTo(service){
console.log(service);
if(!service.url){return false;}

sessionStorage.setItem('intrlligentUrl',service.url);
if(service.url.indexOf('/goods')==0){
window.location.href = '/m/equipmentIndex.html?isBuy=1&goodId='+service.service.id+'&sourceType=0&_='+(new Date().getTime());
}else{
window.location.href = '/m/serviceIndex.html?serviceId='+service.service.id+'&_='+(new Date().getTime());
}
}





















}
}



